.orderLevelHold{
    border-bottom: 1px solid lightgrey;
    padding: 0.5rem 0rem;
}
.holdCategory{
    font-weight: bold;
}
.checklistButton{
    margin-bottom: 0.2rem;
    background-color:#f5f5f5;
    width:97%;
    border-color:#cacaca;
    color:#474747;
    padding:1rem;
    text-align:left;
    font-weight:bold;
    display:inline-block;
    outline: none !important;
}
.shard1, .shard2, .shard3, .shard4, .shard5, .shard6, .shard7, .shard8{
    display: inline-block;
    padding:0rem 0.5rem;
    margin:0rem 0.5rem;
    background-color: #226192;
    border-radius: 7px;
    color:white;
    font-size: 0.8rem;
}
.holdType{
    font-weight: bold;
    font-size: 0.8rem;
    padding:0rem 0.5rem;
    margin:0rem 0.5rem;
}
.queue{
    margin-bottom: 1rem;
}
.queueName{
    background-color: #226192;
    font-size: 0.9rem;
    padding: 0.3rem 0.6rem;
    color:white
}
.queueInfo{
    font-size: 0.7rem;
    background-color: #efefef;
    padding: 0.2rem 0.6rem;
    border-bottom: 1px solid lightgrey;
}