.systemButton{
    padding: 1rem;
    border-left:5px solid #1b73b3;
    color:black;
    font-weight: bold;
    margin-bottom: 1rem;
    background-color: white;
    font-size: 0.9rem;
}
.systemButton:hover{
    cursor: pointer;
}
.statusIcon{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    display: inline-block;
    float: right;
}
.platformRow, .platformRowHead{
    background-color: #f7f7f7;
    border-bottom: 1px solid lightgrey;
    padding: 0.2rem;
    font-size: 0.7rem;
    font-weight: bold;
}
.platformCol{
    color: white !important;
}
.systemDetails{
    width: 100%;
    margin-top: 0rem;
    margin-bottom: 1rem;
    width: 92%;
    margin-left: 4%;
}
.platformRowHead{
    background-color: #bababa;
}