.tab{
    text-decoration: none;
    background-color: white;
    padding: 1rem;
    border-left: 5px solid #226192;
    margin-bottom: 1rem;
}
.myIcon{
    background-color: #226192;
    width:3.5rem;
    height:3.5rem;
    border-radius:1.75rem;
    text-align: center;
    padding-top: 0.5rem;
}
.tab a{
    text-decoration: none;
    color:#226192;
    font-weight: 500;
    outline: none;
}
.tab a:hover{
    text-decoration: none;
    color:#226192;
}
.tabName{
    margin-top:0.7rem;
    font-size: 1.2rem;
    margin-left: 1rem;
}