.bullet{
    width: 22px;
    height: 22px;
    border-radius: 11px;
    background-color: #d38845;
    padding: 4px;
    margin-right: 1rem;
    vertical-align:top;
    margin-top: 0.6rem;
}
.bullet, .cutoffDetails{
    display: inline-block;
}
.cutoffTime{
    font-size: 1.5rem;
}
.cutoffCount{
    font-weight: bold;
}
.cutoffTime, .cutoffDropdown{
    display: inline-block;
}
.cutoffDropdown{
    vertical-align: top;
}
.cutoffSlot{
    margin-bottom: 0.5rem;
}
.categoryHead{
    padding: 0rem 1rem 1.5rem 1rem;
    border: 1px solid #bab7bd;
    background-color:rgba(220,220,220,0.3);
    margin-top: 0.5rem;
}
.cutoffTimeWrapper:hover{
    cursor: pointer;
}
.statusCount{
    font-size: 0.6rem;
    margin-left: 0rem;
    margin-top: 0.5rem;
    background-color: #226192;
    color:white;
    padding: 0.2rem 0.2rem 0.2rem 0.5rem;
    border-radius: 4px 4px 0px 0px;
    font-weight: bold;
    z-index: 100;
}
.statusCount:hover{
    cursor:pointer;
}
.categoryName{
    font-size: 0.7rem;
    font-weight: bold;
    margin-top: 1rem;
}
.summaryCategoryName{
    font-size: 0.8rem;
    font-weight: bold;
}
.summaryStatusCount{
    font-size: 0.6rem;
    font-weight: bold;
    padding: 0.2rem 0.4rem;
    margin-top: 0.4rem;
    color:white;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    border-radius: 4px;
    background-color: #226192;
}
.summarySection{
    margin-bottom: 1rem;
    margin-top: 0.5rem;
    background-color: white;
    min-height:8rem;
    padding: 0.5rem;
    border: 1px solid lightgrey;
    border-radius: 5px;
}
.summaryGraph{
    background-color: white;
    padding-top: 1rem;
    border: 1px solid lightgrey;
    border-radius: 5px;
}
.ordersCount{
    font-size: 3rem;
}
.ordersText{
    font-size: 1.5rem;
    margin-left: 0.5rem;
}
.totalOrdersText{
    background-color: white;
    border: 1px solid lightgrey;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    margin-top: 0.5rem;
}
.omsLoader{
    text-align: right;
}
.loaderImage, .loaderText{
    display:inline-block;
}
.loaderImage img{
    width: 50px;
    filter: grayscale(1);
    margin-left: 0.5rem;
}
.omsLoader{
    margin-top: 2rem;
}
.draftOrders, .createdOrders, .acocOrders, .poCretedOrders,.poAckOrders, .cancelled{
    height: 5px;
    display: inline-block
}
.draftOrders{
    background-color: #8e8e8e;
}
.createdOrders{
    background-color: grey;
}
.acocOrders{
    background-color: #0f6674;
}
.poCretedOrders{
    background-color: #e2c357;
}
.poAckOrders{
    background-color: #4ab14a;
}
.cancelled{
    background-color: #e5594f;
}
.orderStatusLine{
    margin-top: 0rem;
}
.omsStatusDescriptionBox{
    background-color: #f3f3f3;
}
.omsStatusDescription{
    font-size: 0.7rem;
    font-weight: bold;
    margin-left: 0.3rem;
    padding: 0.1rem;
    border-bottom: 1px solid #e1e1e1;
}
.orderStatusLine:hover{
    cursor: pointer;
}